import Link from 'next/link'


const GlobalHeader = props => {

  const siteName = 
    props.isTopPage 
      ? <h1 className="siteName h4 regular m0 siteName">{ props.siteName }</h1> 
      : <Link href="/" as={ `${ props.publicUrl }` }><a className="h4 regular m0 siteName">{ props.siteName }</a></Link>;

  return (
    <header className="global-header py0 px2 shadow-md bg-white">
      <div className="line-height-16">{ siteName }</div>
      <style jsx>{`
        .siteName {
          color: #fff;
        }
        .siteName:hover {
          text-decoration: none;
        }
      `}</style>
    </header>
  );
};

export default GlobalHeader;